.container {
  flex-grow: 2;
  max-width: 33%;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.box {
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  align-items: flex-start;
  width: 100%;
  margin-bottom: 32px;
}

.label {
  color: #10182860;
  font-size: 16px;
  font-weight: 500;
  width: 100%;
}

.inputBox {
  position: relative;
  display: flex;
  flex-direction: row;
  column-gap: 8px;
  align-items: center;
  width: 100%;
  color: #10182860;
  background-color: #f7f7f7;
  border-radius: 10px;
  padding: 18px 0;
}

.svgBox {
  position: absolute;
  left: 18px;
  width: 18px;
  height: 20px;
  fill: none;
  stroke: #10182860;
  padding-top: 4px;
}

.input {
  font-size: 16px;
  font-weight: 400;
  background-color: inherit;
  border: none;
  width: 100%;
  margin-right: 18px;
  text-indent: 44px;
}

.input:focus {
  border: none;
  color: #101828;
}

.input:focus + .svgBox {
  stroke: #101828;
}

.group {
  color: #10182860;
  font-size: 16px;
  font-weight: 500;
  width: 100%;
  margin-bottom: 14px;
}

.gap {
  margin-bottom: 32px;
}

.button {
  max-width: 173px;
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  background-color: #e44848;
  border-radius: 200px;
  padding: 16px 40px;
  cursor: pointer;
}

.button:hover,
.button:focus {
  background-color: #d84343;
}
