.container {
  display: flex;
  flex-direction: row;
  gap: 8px;
  flex-wrap: wrap;
  font-size: 16px;
  color: #101828;
}

.box {
  display: flex;
  flex-direction: row;
  column-gap: 8px;
  color: #101828;
  background-color: #f2f4f7;
  border-radius: 100px;
  padding: 12px 18px;
  align-items: center;
}

.svgBox {
  width: 20px;
  height: 20px;
  fill: none;
  stroke: #101828;
}
